<template>
    <div class="column-container">
        <el-table :data="tableData">
        <el-table-column prop="title" label="" width="140">
        </el-table-column>
        <el-table-column prop="date" label="" width="120">
        </el-table-column>
        <el-table-column prop="author" label="" width = "120">
        </el-table-column>
        <el-table-column prop="content" label="" width = "660">
        </el-table-column>
        <el-table-column prop="handle" label="">
            <template slot-scope="scope">
                <router-link to = "/console/edit">
                     <el-button type="text" size="small"></el-button>
                </router-link>
            </template>
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
export default {
  name: "console-video",
  data() {
    const item = {
      date: "2016-05-02",
      title: "",
      author: "",
      content: "",
    };
    return {
        tableData: Array(6).fill(item)
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>




