import axios from 'axios';

axios.defaults.timeout = 5000;
axios.defaults.baseURL ='localhost:3000'; //

//http request 
axios.interceptors.request.use(
  config => {
    config.data = JSON.stringify(config.data);
    config.headers = {
      'Content-Type':'application/x-www-form-urlencoded'
    }
    return config;
  },
  error => {
    return Promise.reject(err);
  }
);

//
axios.interceptors.response.use(response => {
    return response
}, err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
            console.log('')
          break;
        case 401:
            console.log('')
          break;
        case 403:
          console.log('')
          break;
        case 404:
          console.log(',')
          break;
        case 405:
          console.log('')
          break;
        case 408:
          console.log('')
          break;
        case 500:
          console.log('')
          break;
        case 501:
          console.log('')
          break;
        case 502:
          console.log('')
          break;
        case 503:
          console.log('')
          break;
        case 504:
          console.log('')
          break;
        case 505:
          console.log('http')
          break;
        default:
          console.log(`${err.response.status}`)
      }
    } else {
      console.log('')
    }
    return Promise.resolve(err.response)
})


/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

 export function post(url,data = {}){
   return new Promise((resolve,reject) => {
     axios.post(url,data)
          .then(response => {
            resolve(response.data);
          },err => {
            reject(err)
          })
   })
 }

 /**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}

 /**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}

/**
* 
*/
/** 
* 
* file
* paramObj/null
* fetch/post/patch/put
*/
export const server = {
    file(paramObj){
        return fetch('/api/file',paramObj);
    }
}


